/* eslint-disable jsx-a11y/alt-text */
import { useEffect } from "react";
import Spacer from "../component/Spacer";
import axios from "axios";
import { useState } from "react";
import "./profile.css";
import Navbar from "../component/Navbar/Navbar";
import Footer from "../component/Footer";
import { useDispatch, useSelector } from "react-redux";
import { getUser } from "../services/redux/middleWare/getUser";
import { BounceLoader } from "react-spinners";
import { client } from "../services/client";
import { useNavigate } from "react-router-dom";

export default function EditProfile() {
  const [id, setId] = useState("");
  const [firstName, setFirstName] = useState("");
  const [phoneNo, setPhoneNo] = useState("");
  const [cnic, setCnic] = useState("");

  const [image, setImage] = useState("");
  const [loader, setLoader] = useState(false);
  const navigate = useNavigate();

  const dispatch = useDispatch();
  const state = useSelector((state) => state.getUser.getUserData);
  console.log("state in edit profile", state);

  // useEffect(()=>{
  //   const localid = localStorage.getItem('id');
  //   console.log("i am your local id",localid)
  //   if(localid){
  //     setId(localid);
  //   }

  //   async function getProfile(){
  //     const res = await axios.get('/api/getProfile')
  //     console.log(res.data.data);
  //     for(let i of res.data.data){
  //       if(i.userId === localid){
  //         setFirstName(i.firstName);
  //         setLastName(i.lastName);
  //         setImage(i.image);
  //       }
  //     }

  //   }
  //   getProfile();

  // })

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.readAsDataURL(file);

    reader.onload = () => {
      setImage(reader.result);
      // setSelectedFile(file);
    };
  };
  useEffect(() => {
    const localid = localStorage.getItem("id");
    if (localid) {
      setId(localid);
      dispatch(getUser(localid));
    }
  }, []);
  async function updateProfile() {
    setLoader(true);
    try {
      const res = await client.put(`updateProfile/${id}`, {
        name: firstName ? firstName : state?.Name,
        phoneNo: phoneNo ? phoneNo : state?.PhoneNo,
        image: image ? image : state?.image,
        cnic: cnic ? cnic : state.cnic,
      });
      setLoader(false);
      if (res.status === 200) {
        setLoader(false);
        alert("Profile Updated");
        dispatch(getUser(id));
        navigate("/services");
      }
    } catch (err) {
      console.log(err.response);
      // setLoader(false);
    }
  }
  return (
    <>
      <Navbar />
      <main class="py-6 bg-surface-secondary">
        <div class="wrapper">
          {loader && (
            <div style={{ position: "absolute", top: "50%", left: "50%" }}>
              <BounceLoader size={100} color="#36d7b7" />
            </div>
          )}
          <div class="profile">
            <div class="content">
              <h1>Profile</h1>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <img
                  style={{
                    height: "200px",
                    width: "200px",
                    borderRadius: "100px",
                  }}
                  src={
                    image
                      ? image
                      : state?.image
                      ? state?.image
                      : "https://thumbs.dreamstime.com/b/default-avatar-profile-icon-vector-default-avatar-profile-icon-vector-social-media-user-image-vector-illustration-227787227.jpg"
                    //"https://www.nicepng.com/png/detail/933-9332131_profile-picture-default-png.png"//
                    // "data:image/jpeg;base64,"
                  }
                />
              </div>
              {/* <form action=""> */}
              <fieldset style={{ display: "flex", alignItems: "center" }}>
                <div class="grid-35">
                  <label for="avatar">Your Photo</label>
                </div>

                <div class="grid-65">
                  <input onChange={handleFileChange} type="file" class="btn" />
                </div>
              </fieldset>

              <fieldset
                style={{
                  display: "flex",
                  alignItems: "center",
                  padding: "30px 0px",
                }}
              >
                <div className="grid-35">
                  <label htmlFor="fname">Name</label>
                </div>
                <div className="grid-65">
                  <input
                    value={firstName}
                    onChange={(e) => {
                
                      const alphabeticInput = e.target.value.replace(
                        /[^A-Za-z]/g, "" );

                    
                      const limitedInput = alphabeticInput.slice(0, 273);

                     
                      setFirstName(limitedInput);
                    }}
                    type="text"
                    id="fname"
                    name="fname"
                    tabIndex="1"
                    pattern="[A-Za-z]*" 
                    placeholder={state?.Name ? state?.Name : "Enter Name"}
                    maxLength="273"
                    required
                  />
                </div>
              </fieldset>
              <fieldset
                style={{
                  display: "flex",
                  alignItems: "center",
                  padding: "30px 0px",
                }}
              >
                <div className="grid-35">
                  <label htmlFor="phoneNo">Phone No</label>
                </div>

                <div className="grid-65">
                  <input
                    value={phoneNo}
                    onChange={(e) => {
                      // Remove any non-numeric characters from the input using a regular expression
                      const numericInput = e.target.value.replace(
                        /[^0-9]/g,
                        ""
                      );

                      // Limit the input to 11 characters
                      const limitedInput = numericInput.slice(0, 11);

                      // Update the state variable with the cleaned and limited input
                      setPhoneNo(limitedInput);
                    }}
                    type="tel"
                    id="phoneNo"
                    name="phoneNo"
                    tabIndex="2"
                    pattern="[0-9]*"
                    placeholder={
                      state?.PhoneNo ? state?.PhoneNo : "Enter Phone no"
                    }
                    maxLength="11"
                    required
                  />
                </div>
              </fieldset>
              <fieldset
                style={{
                  display: "flex",
                  alignItems: "center",
                  padding: "30px 0px",
                }}
              >
                <div class="grid-35">
                  <label for="lname">Email</label>
                </div>
                <div class="grid-65">
                  <input
                    disabled
                    type="text"
                    id="lname"
                    tabindex="2"
                    placeholder={state?.Email}
                  />
                </div>
              </fieldset>

              <fieldset
                style={{
                  display: "flex",
                  alignItems: "center",
                  padding: "30px 0px",
                }}
              >
                <div className="grid-35">
                  <label htmlFor="cnic">CNIC</label>
                </div>
                <div className="grid-65">
                  <input
                    value={cnic}
                    onChange={(e) => {
                      // Remove any non-numeric characters from the input using a regular expression
                      const numericInput = e.target.value.replace(
                        /[^0-9]/g,
                        ""
                      );

                      // Limit the input to 13 characters
                      const limitedInput = numericInput.slice(0, 13);

                      // Update the state variable with the cleaned and limited input
                      setCnic(limitedInput);
                    }}
                    type="text"
                    id="cnic"
                    name="cnic"
                    tabIndex="2"
                    pattern="[0-9]*" // Restrict input to numeric characters
                    placeholder={state?.cnic ? state?.cnic : "Enter CNIC"}
                    maxLength="13" // Set maximum length to 13 characters
                    required
                  />
                </div>
              </fieldset>

              <fieldset>
                <input type="button" class="Btn cancel" value="Cancel" />
                <input
                  onClick={() => {
                    updateProfile();
                  }}
                  type="submit"
                  class="Btn"
                  value="Save Changes"
                />
                {/* <button onClick={()=>{updateProfile()}}  class="Btn" value="Save Changes" >Save Changes</button> */}
              </fieldset>
              {/* </form> */}
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
}
