import Navbar from "../component/Navbar/Navbar";
import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { Row, Col, Container } from "react-bootstrap";
import { Card } from "../component/Card";

import { H2, P } from "../component/Typography";
import Button from "../component/Button";
import Spacer from "../component/Spacer";

import Footer from "../component/Footer";

import ModalView from "../component/ModalView";
import ReactStars from "react-rating-stars-component";
import { useDispatch, useSelector } from "react-redux";
import art from "../images/art.jpg";
import adress from "../images/adress.svg";
import mail from "../images/mail.svg";
import Report from "../images/report (2).png";
import { client } from "../services/client";
const Wrapper = styled.div`
  background-color: #f7f7f7;
  padding-top: 70px;
  padding-bottom: 100px;
`;
const CardImages = styled.div`
  position: relative;
`;
const ProfileImage = styled.div`
  position: absolute;
  bottom: -25%;
  left: 36%;
`;
const BottomCard = styled.div`
  // background-color: #f7f7f7;
  // padding-top: 20px;
  // padding-bottom: 20px;
  // border-top: 1px solid black;
`;
export default function Users() {
  const dispatch = useDispatch();

  const [data, setData] = useState();

  useEffect(() => {
    async function fetchData() {
      try {
        const res = await client.get("getUser");
        console.log(res);
        setData(res.data.data);
      } catch (err) {
        console.log(err);
      }
    }
    fetchData();
  }, []);
  return (
    <>
      <Navbar />
      <h1 style={{ textAlign: "center" }}>Users</h1>
      <Row style={{ padding: "0", margin: "0",marginBottom:"30px" }}>
        {data
          ? data?.map((item, index) => {
              if (item.isAdmin === false)
                return (
                  <Col  data-aos="fade-up" key={index} md={3}>
                    <Card style={{ padding: "0px", marginTop: "20px", boxShadow: "0 5px 83px 40px rgb(40 40 40 / 8%)" }}>
                      <CardImages>
                        <img
                          alt="background"
                          style={{
                            borderRadius: "20px",
                            width: "100%",
                          }}
                          // width='100%'
                          width={2000}
                          height={200}
                          src={art}
                        />

                        <ProfileImage>
                          <img
                            alt="profile"
                            style={{ borderRadius: "50px" }}
                            width={100}
                            height={100}
                            src={
                              item.image
                                ? item.image
                                : "data:image/jpeg;base64,"
                            }
                          />
                        </ProfileImage>
                      </CardImages>
                      <br />
                      <br />

                      <H2
                        lineHeight="50px"
                        fontWeight="600"
                        fontSize="30px"
                        color="#0e1b4d"
                      >
                        {item.Name ? item.Name : "Not Updated"}
                      </H2>

                      <br />
                      <Row>
                        <Col style={{ textAlign: "center" }} md={12}>
                          <span style={{ marginRight: "20px" }}>
                            <img
                              alt="Location"
                              style={{ marginRight: "10px" }}
                              width={25}
                              height={25}
                              src={adress}
                            />
                            {item.PhoneNo ? item.PhoneNo : "Not Given"}
                          </span>
                        </Col>
                        <Col style={{ textAlign: "center" }} md={12}>
                          <span>
                            <img
                              alt="Location"
                              style={{ marginRight: "10px" }}
                              width={20}
                              height={20}
                              src={mail}
                            />
                            {item.Email}
                            {/* {item.status==="none"?"03*********":item.status==="pending"?"03******":item.contact} */}
                          </span>
                        </Col>
                        <Col style={{ textAlign: "center" }} md={12}>
                          <span>
                            {item.isWorker ? "Worker" : "Customer"}
                            {/* {item.status==="none"?"03*********":item.status==="pending"?"03******":item.contact} */}
                          </span>
                        </Col>
                      </Row>

                      <hr />
                      <Spacer height="6" />

                     
                    </Card>
                  </Col>
                );
            })
          : "loading..."}
      </Row>

      <Footer />
    </>
  );
}
